#loadingscreen {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 0;
  left: 0;
}

.projectdiv {
  flex: 1 1 auto;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.shortFadeIn {
  opacity: 0;
  animation: fadeIn 750ms ease 1;
  animation-fill-mode: forwards;
}

.fadeIn {
  opacity: 0;
  animation: fadeIn 1s ease 1;
  animation-fill-mode: forwards;
  animation-delay: 2250ms;
}

.max {
  height: 100%;
  width: 100%;
}

.maxswitch {
  height: calc(100% - 59px);
  width: 100%;
}

.page {
  flex: 1 1 auto;
  background-color: #fff;
  display: flex;
  align-items: center;
}

.vertical {
  display: flex;
  flex-direction: column;
}

.horizontal {
  display: flex;
  flex-direction: row;
}

.centerVertically {
  align-items: center;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    pointer-events: none;
  }
  100% {
    opacity: 1;
    pointer-events: all;
  }
}

.projectrow {
  display: flex;
  flex-direction: row;
}

svg.contact {
  height: 100px;
  width: 100px;
}

div#contact {
  flex-direction: row-reverse;
}

.resume {
  z-index: 2;
  max-width: 750px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  padding: 1em;
  flex-direction: column;
}

.line {
  width: 100%;
  border-right-style: initial;
  border-bottom-style: initial;
  border-left-style: initial;
  border-right-color: initial;
  border-bottom-color: initial;
  border-left-color: initial;
  margin-bottom: 1em;
  border-width: 4px 0px 0px;
  border-image: initial;
  border-top: 4px solid rgb(0, 0, 0);
}

.categoryHeading {
  color: black;
  font-size: 32px;
  line-height: 1;
  font-family: "Raleway";
  font-weight: 900;
  margin-top: 0.7em;
  margin-bottom: 0.3em;
}

.nameStyle {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-size: 48px;
  font-family: "Raleway";
}

.paragraphStyle {
  color: #30343f;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Raleway";
  margin: 0;
}

.buttonStyle {
  cursor: pointer;
  pointer-events: all;
  color: #48639c;
  font-weight: 900;
}

.buttonStyle:hover {
  color: #fe938c;
}

.splitTextDiv {
  display: flex;
  -webkit-box-pack: justify;
  margin: 0;
  padding: 0;
  justify-content: space-between;
  flex-direction: row;
}

ul {
  margin: 0;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
